<template>
    <div class="title">
        <h1>Crax Discount</h1>
        <div class="inline">
            <div style="display: inline;">
                <input placeholder="Discount ID" class="form_input" style="width: 15%" v-model="getField"
                       name="get_field" id="get_field">
            </div>
            <div style="display: inline;">
                <button class="customer_button" style="background: #092e3e;" @click="getDiscount">Get Discount</button>
            </div>
            <div style="display: inline;">
                <input placeholder="Discount ID" class="form_input" style="width: 13%" v-model="deleteField"
                       name="first_field" id="first_field">
            </div>
            <div style="display: inline;">
                <button class="customer_button" @click="deleteDiscount">Delete Discount</button>
            </div>
        </div>
        <hr class="gap">
        <textarea class="customer_details" id="customer_details" rows="10" v-model="discountDetails"></textarea>
        <div class="form_block">
            <button class="customer_button" @click="updateDiscount">Update Discount</button>
            |
            <button class="customer_button" style="background: #092e3e;" @click="createCustomer">Create Discount
            </button>
        </div>
    </div>
</template>
<script>
    import {CORE_HTTP} from "../axiosBase"

    export default {
        name: 'Discount',
        data() {
            return {
                getField: '',
                deleteField: '',
                discountDetails: ''
            }
        },
        methods: {
            getDiscount() {
                if (this.getField) {
                    CORE_HTTP().get('/discount/' + this.getField + '/').then(response => {
                        this.discountDetails = JSON.stringify(response.data)
                    }).catch(e => {
                        this.discountDetails = e
                    })
                }
            },
            createCustomer() {
                if (this.discountDetails) {
                    CORE_HTTP().post('/discounts/', {discount: this.discountDetails}).then(response => {
                        this.discountDetails = JSON.stringify(response.data)
                    }).catch(e => {
                        this.discountDetails = e
                    })
                }
            },
            deleteDiscount() {
                if (this.deleteField) {
                    CORE_HTTP().delete('/discount/' + this.deleteField + '/').then(response => {
                        if (response.status === 204) {
                            this.discountDetails = JSON.stringify({'success': 'Deleted'})
                        }
                    }).catch(e => {
                        this.discountDetails = e
                    })
                }
            },
            updateDiscount() {
                if (this.discountDetails && this.getField) {
                    CORE_HTTP().patch('/discount/' + this.getField + '/',
                        {discount: this.discountDetails}).then(response => {
                        if (response.status === 204) {
                            this.discountDetails = JSON.stringify({'success': 'Updated'})
                        }
                    }).catch(e => {
                        this.discountDetails = e
                    })
                }
            }
        }
    }
</script>