<template>
    <div class="title">
        <h1>Crax Customers</h1>
        <textarea id="customer_details" rows="10" class="customer_details" v-model="customerDetails"></textarea>
        <br>
      <textarea class="customer_details" id="error_block" v-if="customersErrors" v-model="customersErrors"></textarea><br>
        <a id="get_customers_list" class="customer_button" style="background: #092e3e;" @click="getCustomer">Get Customer List</a> |
        <a id="create_customers" class="customer_button" @click="createCustomer">Create Customers</a>
    </div>
</template>
<script>
    import {CORE_HTTP} from "../axiosBase"

    export default {
        name: 'Customers',
        data() {
            return {
                pathParameters: {},
                customerPath: '/customers/',
                customerDetails: '',
                customersErrors: ''
            }
        },
        methods: {
            getCustomer() {
                CORE_HTTP().get(this.customerPath).then(response => {
                    this.customerDetails = JSON.stringify(response.data)
                })
            },
            createCustomer() {
                if (this.customerDetails !== '') {
                  try {
                     CORE_HTTP().post(this.customerPath, {users: this.customerDetails}).then(response => {
                        this.customerDetails = JSON.stringify(response.data)
                    }).catch(e => {this.customersErrors = e; setTimeout(() => {this.customersErrors = ''}, 2500)})
                  } catch (e) {
                    this.customersErrors = e
                    setTimeout(() => {this.customersErrors = ''}, 2500)
                  }
                }
            }
        },
        mounted() {
            if ('customer_id' in this.$route.params) {
                this.customerPath = '/customer/' + this.$route.params['customer_id'] + '/'
            }
        },
        watch: {
            customerPath() {
                this.getCustomer()
            }
        }
    }
</script>
