<template>
    <div class="title">
        <h1>Crax Cart</h1>
        <div class="form_block">
            <form enctype="multipart/form-data">
                <input class="form_input" v-model="firstField" name="first_field" id="first_field">
                <input class="form_input" v-model="secondField" name="second_field" id="second_field">
                <input class="form_input" ref="pic" type="file" style="padding: 0" name="pic" id="pic">
            </form>
            <button class="customer_button" @click="sendRequest">Upload Picture</button>
        </div>
        <textarea id="response_details" rows="10" class="customer_details" v-if="responseDetails" v-model="responseDetails"></textarea>
    </div>
</template>
<script>
    import {CORE_HTTP} from "../axiosBase"
    export default {
        name: 'Cart',
        data() {
            return {
                firstField: '',
                secondField: '',
                responseDetails: ''
            }
        },
        methods: {
            sendRequest() {
                var manualPageData = new FormData()
                manualPageData.append('pic', this.$refs['pic'].files[0])
                manualPageData.append('firstField', this.firstField)
                manualPageData.append('secondField', this.secondField)
                CORE_HTTP().post('/cart/', manualPageData).then(response => {
                    this.responseDetails = JSON.stringify(response.data)
                }).catch(e => {this.responseDetails = e})
            }
        }
    }
</script>
