import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home'
import Cart from './views/Cart'
import Discount from './views/Discount'
import Customer from './views/Customer'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/v1/cart/',
      name: 'cart',
      component: Cart,
      meta: { layout: 'cart' }
    },
     {
      path: '/v1/discounts/',
      name: 'discounts',
      component: Discount,
      meta: { layout: 'discount' }
    },
    {
      path: '/v1/discount/:discount_id/:optional?',
      name: 'discount',
      component: Discount,
      meta: { layout: 'discount' }
    },
    {
      path: '/v1/customers/',
      component: Customer,
      meta: { layout: 'customer' }
    },
    {
      path: '/v1/customer/:customer_id/',
      component: Customer,
      meta: { layout: 'customer' }
    }
  ]
})
